import Modeler from 'bpmn-js/lib/Modeler';

import PropertiesPanel from './properties-panel';

import customModdleExtension from './moddle/custom.json';

import diagramXML from './diagram.bpmn';

import composition from './js/composition.js';

const $modelerContainer = document.querySelector('#modeler-container');
const $propertiesContainer = document.querySelector('#properties-container');

var jQuery = require("jquery");

const modeler = new Modeler({
  container: $modelerContainer,
  moddleExtensions: {
    custom: customModdleExtension
  },
  keyboard: {
    bindTo: document.body
  }
});

const propertiesPanel = new PropertiesPanel({
  container: $propertiesContainer,
  modeler
});

createNewDiagram();

function createNewDiagram(){
	modeler.createDiagram();
	setTimeout(function(){addPool()});
}


function addPool(){
	
	var elementFactory = modeler.get('elementFactory');
    var modeling = modeler.get('modeling');
    var canvas = modeler.get('canvas');

	var parent = canvas.getRootElement();
  	var participant = elementFactory.createParticipantShape();

	modeling.createShape(participant, { x: 400, y: 200 }, parent);

}

window.newComposition = () =>{
	createNewDiagram();
}

window.showConfig = () => {
	composition.showConfig();
}

window.saveConfig = () => {
	composition.saveConfig();
}

window.addMicroservice = (name) => {
	composition.addMicroservice(name, modeler);
}

window.addOperation = (name, url, method) => {
	composition.addOperation(name, url, method, modeler);
}

window.downloadBPMN = (id) => {
	modeler.saveXML({ format: true }, function(err, xml) {
		if (err) {
			showMessage("Error","Could not save BPMN 2.0 diagram");
			return console.error('Could not save BPMN 2.0 diagram', err);
		}

		console.log(xml);

		var element = document.createElement('a');
		element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(xml));
		element.setAttribute('download', id+".bpmn");

		element.style.display = 'none';
		document.body.appendChild(element);

		element.click();

		document.body.removeChild(element);

		$("#sendComposition-dialog").modal('hide');
	});
}

window.checkBPMN = () => {
	const definitions=modeler.get('canvas').getRootElement().businessObject.$parent;

	var hayPools=false;
	var hayNombres=true;
	jQuery.each(definitions.rootElements, function(index, element){
			if (element.$type=="bpmn:Collaboration"){
				hayPools=true;
				jQuery.each(element.participants, function(index, participant){
					if(participant.name==undefined || participant.name==""){
						hayNombres=false
					}
				});
			}
	});

	if(!hayPools){
		showMessage("Error","Pools representing microservices must be defined");
		return false;
	}

	if(!hayNombres){
		showMessage("Error","Some pools are not assigned to a microservice");
		return false;
	}

	return true;
}


window.sendBPMN = (id) => {

	var managerUrl=localStorage.getItem("managerUrl");
	var url=managerUrl+(managerUrl.charAt(managerUrl.length-1)=="/"?"":"/")+"compositions"

	var canvas = modeler.get('canvas'),
        modeling = modeler.get('modeling');

   	const definitions=modeler.get('canvas').getRootElement().businessObject.$parent;
   	definitions.id=id;
   	definitions.name=name;

	modeler.saveXML({ format: true }, function(err, xml) {
		if (err) {
			showMessage("Error","Could not save BPMN 2.0 diagram");
			return console.error('Could not save BPMN 2.0 diagram', err);
		}


		jQuery.ajax({
			url:url,
			method:"POST",
			data:JSON.stringify({
				"id":id,
				"name":name,
				"xml":xml
			}),
			headers:{
				"Content-Type":"application/json"
			},
			success: function(){
				console.log("Composition sent successfully")
				$("#sending-loader").css("display","none");
				$("#sendComposition-dialog").modal('hide');

				showMessage("Congratulations","Composition sent successfully")
			},
			error:function(jx, status, error){
				console.log(error);
				showMessage("Error",error);
			}
		});

    });
}

window.showMessage = (title,text) =>{
	$("#message-title").text(title);
	$("#message-text").text(text);
	$("#message-dialog").modal();
}

let fileReader;

const fileRead = (e) =>{
	modeler.importXML(fileReader.result);
	$("#upload-dialog").modal('hide');
}

window.onChangeHandler = (file) =>{
	fileReader=new FileReader();
	fileReader.onloadend=fileRead;
	fileReader.readAsText(file.target.files[0]);
}

window.zoomin = () =>{
	modeler.get('canvas').zoom(modeler.get('canvas').zoom()+0.1);
}

window.zoomout = () =>{
	modeler.get('canvas').zoom(modeler.get('canvas').zoom()-0.1);
}


