function showConfig(){
	if(localStorage.getItem("serviceServerUrl")!=null && localStorage.getItem("serviceServerUrl").length>0) $("#serviceServerUrl").val(localStorage.getItem("serviceServerUrl"));
	if(localStorage.getItem("managerUrl")!=null && localStorage.getItem("managerUrl").length>0) $("#managerUrl").val(localStorage.getItem("managerUrl"))
	$('#config-dialog').modal();
}

function saveConfig(){
	localStorage.setItem("serviceServerUrl", $("#serviceServerUrl").val().trim());
	localStorage.setItem("managerUrl", $("#managerUrl").val().trim());
	$('#config-dialog').modal('hide');
	if(localStorage.getItem("serviceServerUrl")!=null && localStorage.getItem("serviceServerUrl").length>0){
		loadMicroservices();
	}
  }


function loadConnectionData(){
	var url = location.protocol+'//'+location.hostname+(location.port ? ':'+location.port: '');

	jQuery.ajax({
			url:url+"/serviceregistry",
			dataType:"json",
			success: function(serviceRegistry){
				localStorage.setItem("serviceServerUrl", serviceRegistry.url);
				localStorage.setItem("serviceServerType", serviceRegistry.type);
				localStorage.setItem("managerUrl", url);
				loadMicroservices();
			}
		});
}

function loadMicroservices(){
	var serviceServerUrl=localStorage.getItem("serviceServerUrl");
	if(localStorage.getItem("serviceServerType")=="eureka"){
		var url=serviceServerUrl+(serviceServerUrl.charAt(serviceServerUrl.length-1)=="/"?"":"/")+"eureka/apps"
		jQuery.ajax({
			url:url,
			headers:{
				"Accept":"application/json"
			},
			dataType:"json",
			success: function(microservices){
				var html="";
				var urls={};
				$("#microservices-dialog ul").empty();
				jQuery.each(microservices.applications.application, function(index, microservice){
					var microserviceName=microservice.name;
					var host=microservice.instance[0].hostName;
					var port=microservice.instance[0].port.$;
					urls[microserviceName]="http://"+host+":"+port+"/operations";
					$("#microservices-dialog ul").append('<li class="list-group-item"><a href="#" onClick="window.addMicroservice(\''+microserviceName+'\')">'+microserviceName+'</a></li>');
				})
				sessionStorage.setItem("urls",JSON.stringify(urls));
			}
		});
	}
}

function addMicroservice(name, modeler){
	addPropertyVaue(name, modeler);
}

function addOperation(name, url, method, modeler){
	addPropertyVaue(name, modeler, url, method);
}

function addPropertyVaue(value, modeler, url, method){

    $(".microservices-dialog").css("display","none");
    $("#operations-loader").css("display","inline");
    var element=document.querySelector('#propertyValue');
    element.value=value;

    var selectedElement = modeler.get('selection').get()[0];

	const modeling = modeler.get('modeling');
    

    modeling.updateLabel(selectedElement, value);

    if(url!=undefined && method!=undefined){
    	var camundaNs = 'http://camunda.org/schema/1.0/bpmn';

		const moddle= modeler.get('moddle');
	    var extensionElements = moddle.create('bpmn:ExtensionElements');

	    var field1=moddle.createAny('camunda:field',camundaNs, {name:"url", stringValue:url});
	    var field2=moddle.createAny('camunda:field',camundaNs, {name:"method", stringValue:method});

	    extensionElements.get('values').push(field1);
		extensionElements.get('values').push(field2);
	   
	   	modeling.updateProperties(selectedElement, {'extensionElements':extensionElements});
	}
		
}

module.exports = {
  showConfig:showConfig,
  saveConfig:saveConfig,
  addMicroservice:addMicroservice,
  addOperation:addOperation,
};