import { is } from 'bpmn-js/lib/util/ModelUtil';

import React, { Component } from 'react';

import './PropertiesView.css';

var jQuery = require("jquery");

export default class PropertiesView extends Component {

  constructor(props) {
    super(props);

    this.state = {
      selectedElements: [],
      element: null
    };
  }

  componentDidMount() {

    const {
      modeler
    } = this.props;

    modeler.on('selection.changed', (e) => {

      const {
        element
      } = this.state;

      this.setState({
        selectedElements: e.newSelection,
        element: e.newSelection[0]
      });

      if(e.newSelection.length!=1){
        var dialog=document.querySelector('#microservices-dialog');
        dialog.style.display = "none";

        dialog=document.querySelector('#operations-dialog');
        dialog.style.display = "none";
      }
    });


    modeler.on('element.changed', (e) => {

      const {
        element
      } = e;

      const {
        element: currentElement
      } = this.state;

      if (!currentElement) {
        return;
      }

      // update panel, if currently selected element changed
      if (element.id === currentElement.id) {
        this.setState({
          element
        });
      }

    });
  }

  render() {

    const {
      modeler
    } = this.props;

    const {
      selectedElements,
      element
    } = this.state;

    return (
      <div>

        {
          selectedElements.length === 1
            && <ElementProperties modeler={ modeler } element={ element } />
        }

        {
          selectedElements.length === 0
            && <span>Please select a service task.</span> 
        }

        {
          selectedElements.length > 1
            && <span>Please select a single service task.</span>
        }
      </div>
    );
  }

}


function ElementProperties(props) {

  let {
    element,
    modeler
  } = props;

  if (element.labelTarget) {
    element = element.labelTarget;
  }

  function updateName(name) {
    const modeling = modeler.get('modeling');

    modeling.updateLabel(element, name);
  }


  function showOperationDialog(){
      

      var processID=element.businessObject.$parent.id;

      var microserviceName=null;
      var participants=false;
      jQuery.each(element.businessObject.di.$parent.bpmnElement.participants, function(index, participant){

        if(participant.processRef.id==processID){
            participants=true;
            microserviceName=participant.name!=undefined?participant.name:null;
        }
      });

      if(!participants){
        window.showMessage("Error","First, you must add a pool that represnts a microservice.");

      }else if(microserviceName!=null){

        $(".microservice-name").html(microserviceName);

        var urls=JSON.parse(sessionStorage.getItem("urls"));

        var url=urls[microserviceName];

        $("#operations-dialog ul").empty();
        var dialog=document.querySelector('#operations-dialog');
        dialog.style.display = "block";

        jQuery.getJSON(url, function(operations){
          jQuery.each(operations, function(index,operation){
            jQuery("#operations-dialog ul").append('<li class="list-group-item"><a href="#" onClick="window.addOperation(\''+operation.ID+'\',\''+operation.URL+'\',\''+operation.METHOD+'\')">'+operation.ID+'</a></li>');
          })
        }).done(function(){
          var loader=document.querySelector('#operations-loader');
          loader.style.display = "none";
        })

      }else{
        //alert("You must associate a microservice to the pool of the task.");
        window.showMessage("Error","You must associate a microservice to the pool.");
      }

  };

  function showMicroserviceDialog(){
      var dialog=document.querySelector('#microservices-dialog');
      dialog.style.display = "block";
  };

  return (
    <div className="element-properties" key={ element.id }>

      {
        /*<fieldset>
        <label>Name</label>
        <input value={ element.businessObject.name || '' } onChange={ (event) => {
          updateName(event.target.value)
        } } />
      </fieldset>*/
      }

      {
        is(element, 'bpmn:ServiceTask') &&
           <form className="form-inline">
            <label className="col-sm-2 col-form-label">Microservice Operation</label>
            <input id="propertyValue" className="form-control col-sm-3" value={ element.businessObject.get('name') || ''} onChange={ (event) => {
              updateOperation(event.target.value)
            } } />
             <button className="btn btn-primary col-sm-2" onClick={ showOperationDialog } >Select</button>
          </form>
      }
      {
        is(element, 'bpmn:Participant') &&
          <form className="form-inline">
            <label className="col-sm-2 col-form-label">Microservice</label>
            <input id="propertyValue" className="form-control col-sm-3" value={ element.businessObject.name || '' } onChange={ (event) => {
              updateName(event.target.value)
            } } />
             <button className="btn btn-primary col-sm-2" onClick={ showMicroserviceDialog } >Select</button>
          </form>
      }
      {
        !is(element, 'bpmn:ServiceTask') && !is(element, 'bpmn:Participant') &&
            <span>Please select a service task or a pool.</span>

      }

      {
        /*is(element, 'custom:TopicHolder') &&
          <fieldset>
            <label>topic (custom)</label>
            <input value={ element.businessObject.get('custom:topic') } onChange={ (event) => {
              updateTopic(event.target.value)
            } } />
          </fieldset>*/
      }

    { /* <fieldset>
        <label>actions</label>

        {
          is(element, 'bpmn:Task') && !is(element, 'bpmn:ServiceTask') &&
            <button onClick={ makeServiceTask }>Make Service Task</button>
        }

        {
          is(element, 'bpmn:Event') && !hasDefinition(element, 'bpmn:MessageEventDefinition') &&
            <button onClick={ makeMessageEvent }>Make Message Event</button>
        }

        {
          is(element, 'bpmn:Task') && !isTimeoutConfigured(element) &&
            <button onClick={ attachTimeout }>Attach Timeout</button>
        }
      </fieldset>*/}
    </div>
  );
}


// helpers ///////////////////

function hasDefinition(event, definitionType) {

  const definitions = event.businessObject.eventDefinitions || [];

  return definitions.some(d => is(d, definitionType));
}